	module.exports = {"data":{"markdownRemark":{"html":"<p>画像の左側のやつです。(<a href=\"/3066\">ちなみに画像は 2002/10/27 のもの</a>) 初の完全自分専用PC。\nローカル知人の T.N. 氏より8,000円弱で購入した。\nグラボが刺さってる辺り Windows が動作した形跡があるんだろうと思ってはいたが、 HDD が 認識してくれず (認識できる容量を超えていたため？あるいはクラッシュ？) 実際には FDD 専用機に。\n当時もうちょっと財力があればこの PC で Windows を動かせたかもしれないのだが。</p>\n<p>その後、 98 年末に<a href=\"/2922\">新たな PC </a>を入手し、こっちは暫く BASIC 用に使っていたが、 <a href=\"/3007\">PC-9821Cx/S3</a> 入手で完全にお蔵入り、その後 HARD OFF にて 10 円で売却。</p>\n<h2>FM マルチ音源 PC-9801-86</h2>\n<p>YAMAHA YM2608 を搭載した PCM + FM + PSG などを搭載したマルチ音源。\n旧 PC-98 に刺さる極ありふれたサウンドカードだが、当時はこれすらも立派な DTM の音源になった。\n昔の 98 ゲームはみんなこれを基準に作られたくらいだし。\nWindows が表立ってきた頃から衰退しだしたような。</p>\n<p>当時はオリジナル曲やアレンジなどは考えておらず、専ら耳コピ目的のみで DTM をやっていた。</p>\n<h2>製作物・他やってたこと</h2>\n<ul>\n<li>(オリジナルSS)<a href=\"/tag/evil-magic\">EVIL MAGIC</a></li>\n<li>(オリジナルSS)<a href=\"/tag/doron\">勇者ドロン顛末記</a></li>\n</ul>\n<h2>性能</h2>\n<ul>\n<li>CPU:intel386SX(20MHz)→Ti 486SLC2(40MHz)</li>\n<li>RAM:12.6MB</li>\n<li>GPU:Alliance Promotion-6410</li>\n<li>VRAM:1MB</li>\n<li>HDD:1GB (なぜか認識せず)</li>\n<li>DRIVE: 等速 CD-ROM ドライブ (SCSI)、3.5inch 2HD FDD×3 (1基のみ 3mode 対応)</li>\n<li>NET: (NONE)</li>\n<li>SOUND: オンボード (PC-9801-86相当/PCM+FM6音+PSG3音+リズム6音)</li>\n<li>OS: MS-DOS 6.2</li>\n</ul>","frontmatter":{"cover":null,"date":"1997-11-18T20:00:00+09:00","strDate":"1997/11/18","title":"(1997-2003) NEC 98MULTi modelS1 (PC-9821 初代)"}}},"pathContext":{"path":"/ja/blog/1997/11/18-pc-9821/","langKey":"ja"}}

